export default {"name":"碧潭飘雪","img":"\\zaijiagongcha\\bitanpiaoxue\\img_20191122133602b5opt4.png","desc":["碧潭飘雪产于四川峨眉山。本名徐公茶，是四川新津徐金华先生于九十年代左右开发的高级茉莉花茶，采用高山云雾细嫩茶芽与优质茉莉鲜花，采用传统工艺精心窨制而成，后根据其特点得雅名“碧潭飘雪”，茶味清香浓郁。碧潭飘雪采花时间在晴日午后，挑雪白晶莹、含苞待放的花蕾，赶在开放前择花，使茶叶趁鲜抢香，再以手工精心窨制。花，色丽形美，才敢展露在茶客面前。看那叶似鹊嘴，形如秀柳，汤呈青绿，清澈得叶片可数。水面点点白雪，色彩有对比，淡雅适度，此茶不仅淳香可口，更有观赏价值。"],"menus":[{"name":"碧潭飘雪茶叶历史","content":[{"text":"徐金华，碧潭飘雪创制人。"},{"text":"四川省新津县人，既是老革命(高二时，加入中国共产党)，又是知名茶人。他创制并打造了茉莉\n        花茶\n        的品牌──碧潭飘雪。尊称“徐公”，他创制的花茶，亦称“徐公茶”。徐公茶由华夏茶文化专家、新津徐金华先生精工焙制之佳茗。徐公茶雅号碧潭飘雪。采明前上等细嫩碧\n        绿茶\n        芽，配质优形美伏天茉莉鲜朵，手工操作，精工窨制;芽因出云雾山，摄天地气，得个中三味;花因人工手择，洁嫩如雪，自在清芬。发水冲泡，见汤色澄碧，仿佛幽潭，乳花飘忽，浮悬水空，似碧潭飘雪。轻呷细品，齿舌遍香，有绿茶清香味，得茉莉魂。"},{"text":"四川新津徐公茶文化研究所所长，为中华茶人联谊会会员，中国茶叶学会会员，四川省茶文化协会副会长，四川省茶叶学会常务理事。着有《徐公茶品》一书。长期从事教育宣传和文化工作，历任小学、中学校长、宣传干事、县级文化馆长、文化局长、人大常委会委员及教科文卫委主任等职。"},{"text":"业余爱好广泛，寄情传统文化，尤精茶艺、茶道及茶文化研究，多次参加国内外茶事和茶文化活动。"}]},{"name":"碧潭飘雪茶叶特点","content":[{"text":"碧--茶的色;潭--茶碗;飘--花瓣浮飘水面，香味四溢;雪--洁白茉莉。"},{"text":"碧潭飘雪茶叶颜色是清新透亮的绿，上面飘浮着白色的花瓣，茶香花香淡淡的，却经久停留在唇齿之间。"},{"text":"碧潭飘雪外形紧细挺秀，白毫显露，香气持久，回味甘醇。"},{"src":"\\zaijiagongcha\\bitanpiaoxue\\img_20191122133602fqfo2w.jpg"},{"text":"采用早春嫩芽为茶坯，与含苞未放的茉莉鲜花混合窨制而成，花香、茶香交融，并保留干花瓣在茶中。冲泡后茶汤黄亮清澈，朵朵白花漂浮其上如同天降瑞雪，颇具观赏性和美感，香气清悠品味高雅,有浓郁的茉莉花香气，泡饮时应选用盖碗泡饮，可看到就像碧潭上飘了一层雪。品此茶令人赏心悦目。"}]},{"name":"碧潭飘雪加工工艺","content":[{"text":"【具体实施方式】"},{"text":"1.采摘鲜花蕾:采摘时间宜在无雨天午后露水干后进行;按分级、采用单梢选择式手采，采摘隔日待开的花蕾，鲜花蕾应不带叶、柄，不含病虫蕾，无劣产或异味，无其他夹杂物。"},{"text":"2.鲜花蕾装运:使用清洁卫生、透气良好的竹篮等器具盛放鲜花蕾，运送必须及时，避免雨淋，防止温热、机械损伤，严禁有毒、有异味物质污染。"},{"text":"3.鲜花蕾摊放"},{"text":"4.鲜花蕾进厂要分级摊收，分别摊放;"},{"text":"5.摊放以室内自然摊放为主;"},{"src":"\\zaijiagongcha\\bitanpiaoxue\\img_20191122133602flrzbv.jpg"},{"text":"窨花:"},{"text":"1.将鲜花蕾和春芽茶按2:1比例进行窨花;"},{"text":"2.把鲜花蕾和春芽茶均匀混合堆放于清洁卫生场地;"},{"text":"3.堆放8小时后到花蕾开放，花香入茶后筛除花蕾。"},{"text":"4.烘干:用6CH—20型茶叶烘干机对窨花后茶进行烘干，烘培温度为80°C ;"},{"text":"加入花辨:"},{"text":"1.用人工选择接近开放的鲜花蕾去掉花托、花柄，摘成花辨;"},{"text":"2.把花辨和窨花春芽茶按0.3:1的比例混合均匀。"},{"text":"炒花:"},{"text":"1.用平炒锅把花辨和窨花春芽茶进行翻炒，温度控制在120°C ;"},{"text":"2.炒干至含水量达8%为止。"},{"text":"3.包装销售:待茶凉后装入包装销售。"}]},{"name":"碧潭飘雪冲泡方法","content":[{"text":"碧潭飘雪\n        花茶\n        有着很美丽的名字，源自于在冲泡之后，容器中的漂亮景象。大家如果想要看到真正的碧潭飘雪景象，一定要掌握碧潭飘雪的泡法，不然的话不仅会看不到好的效果，还会对茶饮的营养成分造成破坏。"},{"src":"\\zaijiagongcha\\bitanpiaoxue\\img_20191122133602j31hmb.jpg"},{"text":"1、准备泡茶容器"},{"text":"要想让碧潭飘雪的味道真正发挥出来，缺少不了适合的泡茶容器。最好的容器是陶瓷制作而成的，能够将茶叶的清香味道更好的焕发出来，如果家里没有陶瓷容器的话，可以选择盖杯或者是玻璃杯进行冲泡。另外还要准备一个能够拨弄茶叶的小勺子，避免汗液触碰到茶叶影响味道。"},{"text":"2、下投法放茶叶"},{"text":"碧潭飘雪的泡法有很多，最好的方法是下投法，也就说在将容器经过暖杯之后，先将3克到4克的茶叶放入容器底部，然后将八十度做哟的水导致容器里面。下投法能够将碧潭飘雪冲泡得更加彻底，让茶叶和花瓣可以完全的在水中浸泡。需要注意的是，碧潭飘雪的泡法中不可取的是将茶叶煮沸，这样会破坏掉茶叶的营养。"},{"text":"3、泡茶水温要正确"},{"text":"在泡茶的时候有一个重要的因素对冲泡的效果起到了决定性的作用，那就是水温。一定不要用温水来冲泡，这样很难把花茶泡开。正确的碧潭飘雪的泡法是使用开水，水烧开之后稍等片刻，就可以沏茶了。"},{"text":"4、稍加停顿再冲水"},{"text":"碧潭飘雪的泡法有一个小窍门，就是不要一次性的将开水全部放入容器中，开始的时候导入三分之一，稍等一会到茶叶被浸泡开之后再将容器里面的水加至接近满杯。"},{"text":"碧潭飘雪的泡法正确与否，和冲泡效果是否理想有着直接的联系，如果你想要品尝到最纯正的花茶，就要按照正确的方法来操作，香气清幽的碧潭飘雪会让你流连忘返。"}]},{"name":"碧潭飘雪功效作用","content":[{"text":"1、止痛作用："},{"text":"掺杂其中的茉莉花瓣，其含有的挥发油性物质，能起到理气止痛、解郁散结的作用，是止痛的食疗佳品。"},{"text":"2、安神作用："},{"text":"茶叶能起到调节身心，舒缓压力的作用，效果甚佳。"}]},{"name":"碧潭飘雪鉴别好茶","content":[{"text":"1、外形"},{"text":"最好选用明前茶做茶胚，干茶外形，条索细紧，绿而带黄、布满银毫，显毫匀齐，茶芽显露，有锋苗，色泽绿黄匀润。"},{"text":"2、香气"},{"text":"碧潭飘雪茶汤淡雅，清香纯正，鲜灵持久;品饮茶汤：滋味，鲜爽，回味甘醇。"},{"text":"3、叶底"},{"text":"(开汤后)花干洁白，黄绿明亮，细嫩多芽，以一芽一叶为最佳。"},{"text":"碧潭飘雪色泽清新透亮的绿，上面飘浮着白色的花瓣，茶香花香淡淡的，啜饮一口，茶汤顺滑，饱满度好，花香渗透至唇齿之间，久久不会散去。"}]}]}